"use client";
import useCookies from "@/hooks/useCookies";
import { PauseIcon, PlayCircleIcon, PlayIcon } from "lucide-react";
import React, { useEffect, useState } from "react";

const PlayButton = ({
	isShowing,
	handlePlayButtonClick,
	isPlaying,
	isPaused,
	player,
}: {
	isShowing: boolean;
	handlePlayButtonClick: () => void;
	isPlaying: boolean;
	isPaused: boolean;
	player: Spotify.Player;
}) => {
	const { getCookie, setCookie } = useCookies();

	const [playerState, setPlayerState] = useState<Spotify.Player | null>();

	useEffect(() => {
		player.getCurrentState().then((state) => {
			console.log(state);
		});
	}, [player]);

	async function play() {
		const token = JSON.parse(getCookie("token") || "{}")?.access_token;
		const device_id = getCookie("device_id");

		if (!token || !device_id) {
			console.error("Missing token or deviceId");
			return;
		}

		await fetch(
			`https://api.spotify.com/v1/me/player/play?device_id=${device_id}`,
			{
				method: "PUT",
				headers: {
					Authorization: `Bearer ${token}`,
					"Content-Type": "application/json",
				},
				body: JSON.stringify({ uris: [spotifyTrack.uri] }),
			}
		);
	}

	return (
		<div
			className={`play-button ${isShowing ? "showing" : ""}`}
			onClick={handlePlayButtonClick}
		>
			{isPlaying ? <PauseIcon /> : <PlayIcon />}
		</div>
	);
};

export default PlayButton;
